export default {
	data() {
		return {};
	},
	methods: {
		appOAuthLogin(e) {},
		// 微信小程序端微信登录
		wxLogin(e) {
			let user = e.detail.userInfo
			console.log(e);
			// 微信登录
			uni.login({
				provider: 'weixin',
				success: res => {
					// 获得微信登录的授权码
					let code = res.code
					uni.request({
						url: `${this.$api}/stu/mpWXLogin/${code}`,
						method: 'POST',
						header: {
							'qq': '7929290'
						},
						data: {
							avatarUrl: user.avatarUrl,
							nickName: user.nickName,
							whichMP: 1
						},
						success: res => {
							console.log(res);
							if (res.data.status === 200) {
								
							}
						}
					})
				}
			})
		},
		formSubmit(e) {
			let username = e.detail.value.username
			let password = e.detail.value.password
			uni.request({
				url: `${this.$api}/user/registOrLogin?qq=7929290`,
				method: 'POST',
				data: {
					username: username,
					password: password
				},
				'contentType': 'application/json;charset=utf-8',
				success: res => {
					if (res.data.status === 200) {
						let user = res.data.data;
						console.log(res);
						uni.setStorageSync('user', user)
						uni.showToast({
							icon:'success',
							title:'登录成功'
						})
						setTimeout(() => {
							uni.switchTab({
								url: '/pages/me/me'
							});
						}, 1000)
					} else if (res.data.status === 500) {
						uni.showToast({
							title: res.data.msg,
							image: '../../static/icos/error.png'
						})
					}
				}
			});
		}
	}
};
